let _self;
export default {
	data() {
		return {
			appId: '',
			code_wx: '',
			code_1: '《',
			code_2: '》'
		};
	},
	onLoad(options) {
		_self = this;
		// console.log(options);
		// 微信端

		uni.login({
			success(res) {
				console.log(res.code);
				_self.code_wx = res.code;
				// uni.request({
				// 	url: 'http://localhost:8080/wx/getappid/?code=' + res.code,
				// 	method: 'POST',
				// 	success: res => {
				// 		console.log(res.data.data);
				// 		_self.appId = res.data.data;
				// 	},
				// 	fail: () => {},
				// 	complete: () => {}
				// });
			}
		});







































































	},
	methods: {
		getUserInfo(res) {
			console.log(res);
			let wxdata = {
				code: this.code_wx, //临时登录凭证
				rawData: res.detail.rawData, //用户非敏感信息
				signature: res.detail.signature, //签名
				encrypteData: res.detail.encryptedData, //用户敏感信息
				iv: res.detail.iv //解密算法的向量
			};
			console.log(wxdata)
			uni.request({
				url: 'http://localhost:8080/wx/login',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				data: wxdata,
				success: res => {
					console.log(res);
					if (res.data.status == 200) {
						// 7.小程序存储skey（自定义登录状态）到本地
						// wx.setStorageSync('userInfo', userInfo);
						// wx.setStorageSync('skey', res.data.data);
						console.log('以下是登录成功的信息!!');
						console.log(res);
					} else {
						console.log('服务器异常');
					}
				},
				fail: error => {
					//调用服务端登录接口失败
					console.log(error);
				},
				complete: () => {}
			});
		}
	}
};
